@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;700&family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{display:flex;flex-direction:row;align-items:center;justify-content:center;margin:auto;min-height:80vh;height:-webkit-max-content;height:-moz-max-content;height:max-content}.FXLatestPriceComponent{height:720px;width:400px;padding:20px;display:flex;flex-direction:column;justify-content:space-between;box-shadow:0px 0px 20px #00000050}.FXLatestPriceComponent .Header{display:grid;grid-template-columns:33% 33% 33%;grid-template-rows:auto;justify-items:center;align-items:center}.FXSignalChartComponent{display:flex;flex-direction:column;justify-content:space-between;height:720px;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin-left:20px}.latestPriceContainer{display:flex;flex-direction:column;height:620px}.latestPriceContainer .latestPrice{display:grid;grid-template-columns:33% 33% 33%;justify-items:center;align-items:center;transition:all 200ms ease;text-decoration:none;height:56.36364px;color:currentColor}.latestPriceContainer .latestPrice.active{background:#ecfbff;-webkit-backdrop-filter:blur(0.8);backdrop-filter:blur(0.8)}.latestPriceContainer .latestPrice:hover{background-color:#f5f5f5}.latestPriceContainer .latestPrice .symbol,.latestPriceContainer .latestPrice .price,.latestPriceContainer .latestPrice .change{padding:5px;font-weight:600}.latestPriceContainer .latestPrice{cursor:pointer}.latestPriceContainer .latestPrice .change .positive{color:#00BAEC}.latestPriceContainer .latestPrice .change .negative{color:tomato}*{margin:0;padding:0;box-sizing:border-box}@media all and (max-width: 900px){.container{flex-direction:column}.container .FXLatestPriceComponent{width:90%}.container .FXSignalChartComponent{width:90%;margin:0;margin-top:20px}}

.chart{width:100%;height:100%}.chart-wrapper{width:600px;height:300px;box-shadow:0px 0px 20px #00000050;padding:20px}.custom-tooltip{background:#ffffff;box-shadow:0px 0px 10px #00000050;padding:10px}.custom-tooltip .label-price .price{font-weight:700}.custom-tooltip .label-date{font-weight:700}.loader{width:100%;height:100%;display:flex;align-items:center;justify-content:center}.loader .spinner{height:50px;width:50px;background:transparent;border:4px solid  #00BAEC;border-radius:50px;border-top:4px solid  #ffffff;-webkit-animation:spin .5s linear infinite;animation:spin .5s linear infinite}@media all and (max-width: 900px){.chart-wrapper{width:100%}.chart-wrapper .apexcharts-svg{width:100%}}

.fx-signal-component{height:400px;width:600px;box-shadow:0px 0px 20px #00000050}.fx-signals{height:100%;width:100%;position:relative}.fx-signals .signal{height:100%;width:100%;position:absolute;opacity:1;transition:all 200ms ease-in-out;background-color:#ffffff;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:20px}.fx-signals .signal .pair{font-weight:700}.fx-signals .signal .summary{font-size:3rem;font-weight:700}.fx-signals .signal .count{display:flex;flex-direction:row;font-weight:700}.fx-signals .signal .count .buy{margin:10px;color:#00BAEC}.fx-signals .signal .count .sell{margin:10px;color:tomato}.fx-signals .signal .count .neutral{margin:10px;color:gray}.fx-signals .signal:hover{opacity:0}.fx-signals .indicator-header-wrapper{height:100%;width:100%;padding:20px}.fx-signals .indicator-header-wrapper .Header{display:grid;grid-template-columns:auto 20% 20%;grid-template-rows:auto;justify-items:start;align-items:center}.fx-signals .indicator-header-wrapper .Header h3{padding:10px}.fx-signals .indicator-header-wrapper .indicator-container{margin-top:20px;height:-webkit-max-content;height:-moz-max-content;height:max-content}.fx-signals .indicator-header-wrapper .indicator-container .indicator{display:grid;grid-template-columns:60% 20% 20%;grid-template-rows:auto;justify-items:start;align-items:center;margin-top:10px}.fx-signals .indicator-header-wrapper .indicator-container .indicator .name,.fx-signals .indicator-header-wrapper .indicator-container .indicator .value,.fx-signals .indicator-header-wrapper .indicator-container .indicator .action{padding:5px 10px;font-weight:500}.action.buy,.summary.buy{color:#00BAEC}.action.sell,.summary.sell{color:tomato}.action.neutral,.summary.neutral{color:gray}.error{height:100%;width:100%;line-height:1.6;text-align:justify;display:flex;align-items:center;justify-content:center}@-webkit-keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@media all and (max-width: 900px){.fx-signal-component{width:100%}.fx-signals,.loader,.error{width:100%}.fx-signals .signal,.fx-signals .indicator-header-wrapper,.loader .signal,.loader .indicator-header-wrapper,.error .signal,.error .indicator-header-wrapper{width:100%}}

.App{min-height:100vh;height:-webkit-max-content;height:-moz-max-content;height:max-content;width:100%;scroll-behavior:smooth}header{width:100%;height:10vh;font-size:1.5rem;font-weight:700;padding:10px}footer{width:100%;min-height:10vh;height:-webkit-max-content;height:-moz-max-content;height:max-content;padding:10px}footer small{font-size:.8em;text-align:center}

